.App {
  text-align: center;
  padding-top: 5vh;
  padding-bottom: 10vh;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.filterContainer {
  display: flex;
  flex-direction: column;
}

.filterContainer select {
  height: 10vh;
  font-weight: bold;
  font-size: 14px;
  color: black;
}

.toggle-filter-btn {
  width: 45%;
  height: 10vh;
  font-weight: bold;
  font-size: 16px;
}